import { get, post } from './index';
import { IPhotoItem } from '../types/photo'

export function addOne(url: string): Promise<IPhotoItem> {
  return post('/photos/addOne', { url }) as Promise<IPhotoItem>
}

export function deleteOne(params: { id?: string, url?: string }) {
  return post(`/photos/deleteOne`, params);
}

export function list(params: { page: number, limit: number, sort?: string }): Promise<IPhotoItem[]> {
  return get(`/photos/list`, params) as Promise<IPhotoItem[]>
}

export function findOne(params: { id?: string, url?: string }): Promise<IPhotoItem> {
  return get(`/photos/findOne`, params) as Promise<IPhotoItem>
}

export function isExist(url: string): Promise<boolean> {
  return get(`/photos/isExist`, { url }) as Promise<boolean>
}

export function getRandomLikeUrl(): Promise<string> {
  return get(`/photos/random`) as Promise<string>
}

export function getRndomImgRealUrl(url: string): Promise<string> {
  return get(`/photos/getRandomImgRealUrl`, { url }) as Promise<string>
}

export function checkRandomPhoto(url: string): Promise<{url: string, isExist: boolean}> {
  return get(`/photos/checkRandomPhoto`, { url })
}