import { useEffect } from 'react';
import { useRequest } from 'ahooks';
import { message } from 'antd';
import { getAllTags, deleteTag } from '../../api/tag'
import TagsList from '../../components/TagsList'
import './index.less';

const TagsPage = () => {
  const { data, loading, run } = useRequest(getAllTags, {
    manual: true
  });

  useEffect(() => {
    run();
  }, [])

  const handleRemove = (id: string) => {
    deleteTag(id).then(() => {
      run();
      message.success('删除成功')
    })
  }
  return (
    <TagsList
      isShowEdit
      className="tags-page"
      loading={loading}
      title="标签列表"
      onRemove={handleRemove}
      data={data || []}
      type="tags"
    />
  );
}

export default TagsPage;