import React, { useState, useEffect, useRef, forwardRef, useImperativeHandle, ReactEventHandler } from 'react';
import { message, Popconfirm } from 'antd';
import { FireOutlined, FullscreenExitOutlined, FullscreenOutlined, HeartFilled, HeartOutlined, SwapOutlined, ReloadOutlined } from '@ant-design/icons';
import classNames from 'classnames';

import { getRandomImage } from '../../utils/get';
import { addOne, deleteOne, isExist, getRndomImgRealUrl, checkRandomPhoto, getRandomLikeUrl } from '../../api/photo';
import './index.less';

interface IProps {
  className?: string;
  actionSize?: 'small';
  type?: string;
  onClick?(): void;
  url?: string; // 是否使用此随机链接
  realUrl?: string; // 是否使用此真实链接
  onImageLoad?: ReactEventHandler<HTMLImageElement>;
}

interface IRandomPhotoHandlers {
  change(): void;
}
function RandomPhoto(props: IProps, ref: any) {
  const [currImg, setCurrImg] = useState(props.url || '');
  const [like, setLike] = useState(false);
  const [fullscreen, setFullscreen] = useState(false);
  const [objectFit, setObjectFit] = useState<'cover' | 'contain'>('cover');
  const wrapperRef = useRef();

  async function handleChangeImg() {
    let url = '';
    let isUrlLike = false;
    if (props.type === 'likes') {
      url = await getRandomLikeUrl();
    } else if (props.realUrl) {
      isUrlLike = false;
      url = props.realUrl;
    } else {
      const resp = await checkRandomPhoto(props.url ?? getRandomImage(props.type).json);
      isUrlLike = resp.isExist;
      url = resp.url;
    }
    setCurrImg(url);
    setLike(isUrlLike);
  }

  async function handleClickLike() {
    if (like) {
      await deleteOne({ url: currImg });
      setLike(false);
      message.success('删除成功');
    } else {
      await addOne(currImg);
      setLike(true);
      message.success('添加成功');
    }
  }

  function handleFullScreen() {
    setFullscreen(!fullscreen);
    setObjectFit('contain');
  }

  function handleClickImage() {
    if (fullscreen) {
      handleChangeImg();
    } else if (props?.onClick) {
      props.onClick();
    }
  }

  function handleChangeImageFit() {
    setObjectFit(objectFit === 'cover' ? 'contain' : 'cover');
  }

  useImperativeHandle(ref, () => ({
    change: () => {
      handleChangeImg();
    }
  }));

  useEffect(() => {
    handleChangeImg();
  }, [])


  return (
    <div className={classNames('random-image', props.className, {fullscreen })}>
      <img
        onClick={handleClickImage}
        src={currImg}
        alt="banner"
        referrerPolicy= "no-referrer"
        style={{ objectFit }}
        onLoad={props.onImageLoad}
      />
      <div className={classNames("actions", props.actionSize)}>
        <ReloadOutlined onClick={handleChangeImg} />
        {
          (props.actionSize !== 'small' || fullscreen) &&  <SwapOutlined onClick={handleChangeImageFit} />
        }
        {
          fullscreen ? <FullscreenExitOutlined onClick={handleFullScreen} /> : <FullscreenOutlined onClick={handleFullScreen} />
        }
        {
          (props.actionSize !== 'small' || fullscreen) &&  <FireOutlined />
        }
        {
          like ? (
            <Popconfirm
              title="是否删除此图片？"
              onConfirm={handleClickLike}
            >
              <HeartFilled />
            </Popconfirm>
          ) : <HeartOutlined onClick={handleClickLike} />
        }
      </div>
    </div>
  );
}

export default forwardRef<IRandomPhotoHandlers, IProps>(RandomPhoto);
