import { get, post } from './index';
import { IGifItem, IGifList } from '../types/gif';

export function likeOne(id: string): Promise<IGifItem> {
  return get('/gifs/likeOne', { id }) as Promise<IGifItem>
}

export function unlikeOne(id: string) {
  return get(`/gifs/unlikeOne`, { id });
}

export function getGiftlist(params: { page: number, limit?: number, sort?: string, like: boolean | null }): Promise<IGifList> {
  return get(`/gifs/list`, params) as Promise<IGifList>
}

export function findOne(params: { id?: string, url?: string }): Promise<IGifItem> {
  return get(`/gifs/findOne`, params) as Promise<IGifItem>
}

export function isExist(url: string): Promise<boolean> {
  return get(`/gifs/isExist`, { url }) as Promise<boolean>
}

export function getRandomLikeUrl(): Promise<string> {
  return get(`/gifs/random`) as Promise<string>
}