import { useEffect, useState, ReactEventHandler } from 'react';
import classNames from 'classnames';
import { PageHeader, Descriptions, Select, message } from 'antd';
import { RightCircleOutlined, LeftCircleOutlined, HeartOutlined, HeartFilled } from '@ant-design/icons';
import { getGiftlist, likeOne, unlikeOne } from '../../api/gif';
import { IGifItem } from '../../types/gif';
import './index.less';

interface IColumnState {
  size: number; // 列数
  data: Array<string[]>; // 各列的数据
  heights: number[]; // 各列的高度
  keys: number[]; // 各列的key
  dataIndex: number; //当前渲染的数据索引
  minColumn: number; // 数组最小高度的列数
}
interface ISettingState {
  photoLayout: string; // 图集布局
  gifLikeType: string; // 动图喜欢类型
}

export const GifPage = () => {
  const [data, setData] = useState<{
    list: IGifItem[],
    total: number,
    currPage: number,
  }>({
    list: [],
    total: 9999,
    currPage: 0,
  });
  const [page, setPage] = useState<{
    curr: number,
    size: number,
    list: IGifItem[],
  }>({
    curr: 0,
    size: 1,
    list: [],
  })
  const [columns, setColumns] = useState<IColumnState>({
    size: 0,
    data: [],
    heights: [],
    keys: [],
    dataIndex: 0,
    minColumn: 0,
  });
  const [settingState, setSettingState] = useState<ISettingState>({
    photoLayout: localStorage.getItem('photoLayout') || 'normal',
    gifLikeType: localStorage.getItem('gifLikeType') || 'all',
  });

  function handleChangeColumnSize() {
    const width = document.body.clientWidth;
    let size = Math.ceil(width / 300)
    if (size > 4) {
      size = 4
    }
    if (localStorage.getItem('photoLayout') === 'small') {
      size += 1;
    }
    const heights = [];
    const data = [];
    const keys = [];
    for(let i = 0; i < size; i++) {
      heights.push(0);
      keys.push(Math.random());
      data.push([]);
    }
    setColumns({
      heights,
      keys,
      data,
      size,
      dataIndex: 0,
      minColumn: 0,
    });
  }

  function handleChangePage(changed: number) {
    const curr = (page.curr + changed >= 0) ? page.curr + changed : page.curr;
    if (curr < 0 || data.list.length >= data.total) {
      return;
    }
    const pageData = data.list.slice(curr * page.size, curr * page.size + page.size);
    setPage({
      ...page,
      curr,
      list: pageData,
    })
    if ((curr + 1) * page.size >= data.list.length && changed !== 0) {
      handleLoadImages();
    }
  }

  async function handleLoadImages() {
    // const newData = getRandomImgs(settingState.gifLikeType === 'hybrid' ? '' : settingState.gifLikeType);
    const { list: dataList, total, currPage } = data;
    const likeEnumMap = {
      all: null,
      like: true,
      unlike: false,
    }
    if (dataList.length < total) {
      const resp = await getGiftlist({ page: data.currPage + 1, like : likeEnumMap[settingState.gifLikeType as keyof typeof likeEnumMap]  });
      setData({
        ...data,
        currPage: currPage + 1,
        total: resp.count,
        list: [...dataList, ...resp.data],
      });
    }
  }

  function handleClickLike(item: IGifItem) {
    const isCurrentLike = item.like;
    let callApi = likeOne;
    if (isCurrentLike) {
      callApi = unlikeOne;
    }
    callApi(item._id).then(() => {
      item.like = !item.like;
      setPage({
        ...page,
        list: [...page.list],
      })
      message.success(isCurrentLike ? '不喜欢成功' : '喜欢成功');
    })
  }

  function handleChangeSettingState(key: string, val: string | number) {
    setSettingState({ ...settingState, [key]: val });
    localStorage.setItem(key, val.toString())
  }

  useEffect(() => {
    handleChangeColumnSize();
    handleLoadImages();
    document.title = '动图'
  }, []);

  useEffect(() => {
    if (data.currPage === 1) {
      handleChangePage(0);
    }
  }, [data]);

  return (
    <div className="gifs-page">
      <PageHeader
        ghost={false}
        title="动图"
      >
      <Descriptions>
        <Descriptions.Item label="布局">
          <Select defaultValue={settingState.photoLayout} onChange={(value: string) => handleChangeSettingState('photoLayout', value)}>
            <Select.Option value="normal">常规</Select.Option>
            <Select.Option value="small">紧凑</Select.Option>
          </Select>
        </Descriptions.Item>
        <Descriptions.Item label="喜欢类型">
          <Select defaultValue={settingState.gifLikeType} onChange={(value: string) => handleChangeSettingState('gifLikeType', value)}>
            <Select.Option value="all">全部</Select.Option>
            <Select.Option value="like">喜欢</Select.Option>
            <Select.Option value="unlike">未处理</Select.Option>
          </Select>
        </Descriptions.Item>
      </Descriptions>
    </PageHeader>
      <div className="gifs-wrapper" style={{columnCount: columns.size}}>
        {
          page.list.map((gifItem: IGifItem) => (
            <div className="img-wrapper" key={gifItem._id}>
              <img
                src={gifItem.url.replace('json', 'image')}
                alt="banner"
                referrerPolicy= "no-referrer"
              />
              <div className="actions">
              {
                gifItem.like ? (
                <HeartFilled onClick={() => handleClickLike(gifItem)} />
                ) : <HeartOutlined onClick={() => handleClickLike(gifItem)} />
              }
              </div>
            </div>
          ))
        }
        <div className="pages-change">
          <LeftCircleOutlined className={classNames({disabled: page.curr === 0})} onClick={() => handleChangePage(-1)} />
          <RightCircleOutlined className={classNames({disabled: page.curr === data.list.length})} onClick={() => handleChangePage(1)} />
        </div>
      </div>
    </div>
  )
}

export default GifPage;