import { ImgsList } from '../constants/enums';
import { isDev } from './is';

export function getTimestampFromId(id: string, padding = true) {
  return parseInt(id.substring(0, 8), 16) * (padding ? 1000 : 1);
}

export function getColors() {
  return ['#108ee9', '#2db7f5', '#87d068', '#87e8de', '#ffd591', '#ffadd2', '#d46b08', '#ffe58f', '#adc6ff', '#A19FEE', '#89BAF6', '#83D97C', '#F9B87C', '#8BDBC9', '#FEBC3C', '#25C0BF', '#EF625E', '#F0615F', '#EC6AB9', '#28BFBF', '#FF894D', '#1AC185', '#BD75C8', '#FCBD3E']
}

export function getColorFromName(name: string) {
  const colors = getColors();
  const index = name.split('').reduce((sum, i) => sum + i.charCodeAt(0), 0)
  return colors[index % colors.length];
}

function urlAddRandomQuery(url: string) {
  if (url.indexOf('?') > 0) {
    return `${url}&rd=${Math.random()}`
  } else {
    return `${url}?rd=${Math.random()}`
  }
}

export function getRandomImage(type?: string) {
  const imgType = type || localStorage.getItem('mainImg') || 'fengjing';
  let imgItem = ImgsList[imgType as keyof typeof ImgsList];
  if (!imgItem) {
    const imgsValues = Object.values(ImgsList);
    imgItem = imgsValues[Math.floor(Math.random() * imgsValues.length)];
  }
  let { img, json } = imgItem;
  img = urlAddRandomQuery(img);
  json = urlAddRandomQuery(json);
  return { img, json };
}

export function getRandomImgs(typeName?: string, size = 8) {
  const env = localStorage.getItem('dataMode') === 'development' ? 'dev' : 'pro';
  let tags = Object.values(ImgsList).filter(item => item.env === env);
  if (typeName) {
    tags = tags.filter(item => item.typeName === typeName);
  }
  const result: string[] = []
  if (!tags.length) {
    return result;
  }
  for(let idx = 0; idx < size; idx += 1) {
    const random = Math.floor(Math.random() * tags.length);
    result.push(urlAddRandomQuery(tags[random].json));
  }
  return result;
}