import { useEffect, useState, ReactEventHandler } from 'react';
import { PageHeader, Descriptions, Select } from 'antd';
import RandomPhoto from '../../components/RandomPhoto';
import { getRandomImgs } from '../../utils/get';
import { ImgsList } from '../../constants/enums';
import './index.less';

interface IColumnState {
  size: number; // 列数
  data: Array<string[]>; // 各列的数据
  heights: number[]; // 各列的高度
  keys: number[]; // 各列的key
  dataIndex: number; //当前渲染的数据索引
  minColumn: number; // 数组最小高度的列数
}
interface ISettingState {
  photoLayout: string; // 图集布局
  photoType: string; // 图集类型
}

export const PhotoPage = () => {
  const [data, setData] = useState<string[]>([]);
  const [page, setPage] = useState(1);
  const [columns, setColumns] = useState<IColumnState>({
    size: 0,
    data: [],
    heights: [],
    keys: [],
    dataIndex: 0,
    minColumn: 0,
  });
  const [settingState, setSettingState] = useState<ISettingState>({
    photoLayout: localStorage.getItem('photoLayout') || 'normal',
    photoType: localStorage.getItem('photoType') || 'hybrid',
  });

  function handleChangeColumnSize() {
    const width = document.body.clientWidth;
    let size = Math.ceil(width / 300)
    if (size > 4) {
      size = 4
    }
    if (localStorage.getItem('photoLayout') === 'small') {
      size += 1;
    } else if (localStorage.getItem('photoLayout') === 'large') {
      size -= 1;
    }
    const heights = [];
    const data = [];
    const keys = [];
    for(let i = 0; i < size; i++) {
      heights.push(0);
      keys.push(Math.random());
      data.push([]);
    }
    setColumns({
      heights,
      keys,
      data,
      size,
      dataIndex: 0,
      minColumn: 0,
    });
  }

  // 动态往高度最小的列加一个元素
  function computeColumnsData() {
    const { data: columnsData, minColumn } = columns;
    let dataIndex = columns.dataIndex;
    const newData = [...columnsData];
    if (columns.dataIndex < data.length) {
      newData[minColumn].push(data[dataIndex])
      dataIndex += 1;
      setColumns({
        ...columns,
        dataIndex,
        data: newData,
      })
    }
  }

  function handleImageLoad(e: any, index: number) {
    // 计算新元素的高度
    const height = e.target.height;
    const heights = [...columns.heights];
    heights[index] += height;

    // 找到新元素渲染后高度最小的列
    const minValue = Math.min(...heights);
    const minColumn = heights.indexOf(minValue)

    // 如果还有未渲染的元素，则放入高度最小的列
    let dataIndex = columns.dataIndex;
    const newData = [...columns.data];
    if (dataIndex < data.length) {
      newData[minColumn] = [...newData[minColumn], data[dataIndex]]
      dataIndex += 1;
    }
    setColumns({
      ...columns,
      heights,
      minColumn,
      dataIndex,
      data: newData,
    });
  }

  function handleLoadImages() {
    if (page !== -1) {
      const newData = getRandomImgs(settingState.photoType === 'hybrid' ? '' : settingState.photoType);
      setData([...data, ...newData]);
      setPage(page + 1);
    }
  }

  function handleChangeSettingState(key: string, val: string | number) {
    setSettingState({ ...settingState, [key]: val });
    localStorage.setItem(key, val.toString())
  }

  useEffect(() => {
    computeColumnsData();
  }, [data])

  useEffect(() => {
    handleChangeColumnSize();
    handleLoadImages();
    document.title = '图集'
  }, []);

  return (
    <div className="photos-page">
      <PageHeader
        ghost={false}
        title="图集"
      >
      <Descriptions>
        <Descriptions.Item label="布局">
          <Select defaultValue={settingState.photoLayout} onChange={(value: string) => handleChangeSettingState('photoLayout', value)}>
            <Select.Option value="large">宽松</Select.Option>
            <Select.Option value="normal">常规</Select.Option>
            <Select.Option value="small">紧凑</Select.Option>
          </Select>
        </Descriptions.Item>
        <Descriptions.Item label="类型">
          <Select defaultValue={settingState.photoType} onChange={(value: string) => handleChangeSettingState('photoType', value)}>
              <Select.Option value="hybrid">混合类型</Select.Option>
              {
                Object.keys(ImgsList).map(item => (
                  <Select.Option value={item} key={item}>{item}</Select.Option>
                ))
              }
            </Select>
        </Descriptions.Item>
      </Descriptions>
    </PageHeader>
      <div className="photos-wrapper" style={{columnCount: columns.size}}>
        {
          columns.data.map((column: string[], index: number) => (
            <div className="column" key={columns.keys[index]}>
              {
                column.map((url: string) => (
                  <RandomPhoto key={url} url={url} actionSize='small' onImageLoad={(e) => handleImageLoad(e, index)} />
                ))
              }
            </div>
          ))
        }
      </div>
      <div className="load-more" onClick={handleLoadImages}>{page !== -1 ? '点击加载更多' : "没有更多数据了"}</div>
    </div>
  )
}

export default PhotoPage;