import { Routes, Route, Link } from "react-router-dom";
import Layout from './components/Layout'
import HomePage from './pages/home';
import PostPage from './pages/posts'
import ListPage from './pages/list'
import TagsPage from './pages/tags'
import KeywordsPage from './pages/keywords'
import CollectionsPage from './pages/collections'
import AuthorsPage from './pages/authors'
import SettingPage from './pages/setting'
import PhotoPage from './pages/photos'
import GifPage from './pages/gifs'
import 'antd/dist/antd.css';
import './App.less';

interface RouteItem {
  name: string;
  path: string;
  isMenu: boolean;
  component: any;
  layout: boolean
}
const ROUTES: RouteItem[] = [
  { name: '首页', path: "/", layout: true, isMenu: true, component: HomePage },
  { name: '文章列表', path: "/pages", layout: true, isMenu: true, component: ListPage },
  { name: '标签', path: "/tags", layout: true, isMenu: true, component: TagsPage },
  { name: '标签文章', path: "/tags/:tagName", layout: true, isMenu: false, component: ListPage },
  { name: '关键字', path: "/keywords", layout: true, isMenu: true, component: KeywordsPage },
  { name: '关键字文章', path: "/keywords/:keyword", layout: true, isMenu: false, component: ListPage },
  { name: '图集', path: "/photos", layout: true, isMenu: true, component: PhotoPage },
  { name: '动图', path: "/gifs", layout: true, isMenu: true, component: GifPage },
  { name: '作品集', path: "/collections", layout: true, isMenu: true, component: CollectionsPage },
  { name: '作品集文章', path: "/collections/:collection", layout: true, isMenu: false, component: ListPage },
  { name: '作者', path: "/authors", layout: true, isMenu: true, component: AuthorsPage },
  { name: '作者文章', path: "/authors/:author", layout: true, isMenu: false, component: ListPage },
  { name: '文章内容', path: "/post/:id", layout: true, isMenu: false, component: PostPage },
  { name: '设置', path: "/setting", layout: true, isMenu: true, component: SettingPage },
]

function createRoute(route: RouteItem) {
  if (route.layout) {
    return (
      <Route key={route.name} path={route.path} element={<Layout routes={ROUTES}><route.component /></Layout>} />
    )
  }
  return <Route key={route.name} path={route.path} element={<route.component />} />
}

function App() {
  return (
    <div className="app">
      <Routes>
        <Route path="/test" element={<PostPage />} />
        {
          ROUTES.map(createRoute)
        }
      </Routes>
    </div>
  );
}

export default App;
