import React, { useEffect, ReactChild, ElementRef, useRef } from 'react';
import { Layout, Menu } from 'antd';
import { Link, useLocation, useNavigate } from 'react-router-dom';
import { isMobile } from '../../utils/is';
import RandomPhoto from '../RandomPhoto';
import logo from './logo.svg';
import './index.less';

const { Header, Content, Footer } = Layout;

interface RouteItem {
  name: string;
  path: string;
  isMenu: boolean;
}

interface IProps {
  routes: Array<RouteItem>;
  children: ReactChild | ReactChild[];
}

function AppLayout(props: IProps) {
  const navigate = useNavigate();
  const photoRef = useRef<ElementRef<typeof RandomPhoto>>(null);

  function handleGotoHome() {
    navigate('/');
  }
  function handleChangeBanner() {
    photoRef.current?.change()
  }

  const location = useLocation();
  useEffect(() => {
    handleChangeBanner();
  }, [location]);

  return (
    <Layout className={ isMobile() ? `app-layout mobile` : "app-layout"}>
      <Header className="layout-header">
        <div className="logo" onClick={handleGotoHome}><img src={logo} alt="logo" /></div>
        <Menu
          mode="horizontal"
          defaultSelectedKeys={['2']}
        >
          {props.routes.filter(route => route.isMenu).map(route => (
            <Menu.Item key={route.name}><Link to={route.path}>{route.name}</Link></Menu.Item>
          ))}
        </Menu>
      </Header>
      <Content className="layout-content">
        <header className="content-header">
          <RandomPhoto ref={photoRef} />
        </header>
        <div className="content-inner">
        {props.children}
        </div>
      </Content>
      <Footer style={{ textAlign: 'center' }}>Created by Ant TTBB</Footer>
  </Layout>
  );
}

export default AppLayout;
