import { Card } from 'antd';
import { useNavigate } from "react-router-dom";
import RandomPhoto from '../../components/RandomPhoto';
import './index.less';

const pages = [
  { name: '文章列表', path: "/pages", layout: true, isMenu: true, },
  { name: '标签', path: "/tags", layout: true, isMenu: true, },
  { name: '关键字', path: "/keywords", layout: true, isMenu: true, },
  { name: '作品集', path: "/collections", layout: true, isMenu: true, },
  { name: '作者', path: "/authors", layout: true, isMenu: true, },
  { name: '图集', path: "/photos", layout: true, isMenu: true, },
  { name: '动图', path: "/gifs", layout: true, isMenu: true },
  { name: '设置', path: "/setting", layout: true, isMenu: true, },
]

export const HomePage = () => {
  const navigate = useNavigate();

  function handleClickCard(path: string) {
    navigate(path)
  }

  return (
    <div className="home-page">
    {
      pages.map((page) => (
        <Card
          key={page.path}
          cover={<RandomPhoto actionSize="small" onClick={() => handleClickCard(page.path)} />}

        >
          <Card.Meta title={page.name} />
        </Card>
      ))
    }
    </div>
  )
}

export default HomePage;